"use client"

import { useState } from 'react'
import Link from 'next/link'
import { usePathname } from 'next/navigation'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar'
import { DropdownMenu, DropdownMenuContent, DropdownMenuItem, DropdownMenuLabel, DropdownMenuSeparator, DropdownMenuTrigger } from '@/components/ui/dropdown-menu'
import { Utilisateur } from '@/types'

interface NavigationProps {
  currentUser: Utilisateur
}

export function Navigation({ currentUser }: NavigationProps) {
  const pathname = usePathname()
  
  const isAdmin = currentUser.role === 'ADMINISTRATEUR'
  const isAdminPage = pathname.startsWith('/admin')

  return (
    <nav className="border-b bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60">
      <div className="container mx-auto px-6 py-4">
        <div className="flex justify-between items-center">
          <div className="flex items-center space-x-6">
            <Link href="/" className="text-2xl font-bold text-foreground hover:text-primary transition-colors">
              IdéaBox
            </Link>
            
            <div className="hidden md:flex space-x-4">
              <Link href="/">
                <Button 
                  variant={!isAdminPage ? "default" : "ghost"} 
                  size="sm"
                  className="transition-colors"
                >
                  Accueil
                </Button>
              </Link>
              
              {isAdmin && (
                <Link href="/admin">
                  <Button 
                    variant={isAdminPage ? "default" : "ghost"} 
                    size="sm"
                    className="transition-colors"
                  >
                    Administration
                  </Button>
                </Link>
              )}
            </div>
          </div>

          <div className="flex items-center space-x-4">
            <div className="hidden sm:block text-right">
              <p className="font-semibold text-sm">{currentUser.nom}</p>
              <div className="flex items-center justify-end space-x-2">
                {currentUser.role === 'EMPLOYE' && (
                  <span className="text-xs text-muted-foreground">{currentUser.points} points</span>
                )}
                <Badge variant="outline" className="text-xs">
                  {currentUser.badge}
                </Badge>
              </div>
            </div>

            <DropdownMenu>
              <DropdownMenuTrigger asChild>
                <Button variant="ghost" className="relative h-10 w-10 rounded-full">
                  <Avatar className="h-10 w-10">
                    <AvatarImage src="/placeholder-avatar.jpg" alt={currentUser.nom} />
                    <AvatarFallback>
                      {currentUser.nom.split(' ').map(n => n[0]).join('')}
                    </AvatarFallback>
                  </Avatar>
                </Button>
              </DropdownMenuTrigger>
              <DropdownMenuContent className="w-56" align="end" forceMount>
                <DropdownMenuLabel className="font-normal">
                  <div className="flex flex-col space-y-1">
                    <p className="text-sm font-medium leading-none">{currentUser.nom}</p>
                    <p className="text-xs leading-none text-muted-foreground">
                      {currentUser.email}
                    </p>
                  </div>
                </DropdownMenuLabel>
                <DropdownMenuSeparator />
                
                <DropdownMenuItem asChild>
                  <Link href="/" className="cursor-pointer">
                    Mes Idées
                  </Link>
                </DropdownMenuItem>
                
                {currentUser.role === 'EMPLOYE' && (
                  <DropdownMenuItem>
                    Profil ({currentUser.points} points)
                  </DropdownMenuItem>
                )}
                
                {isAdmin && (
                  <>
                    <DropdownMenuSeparator />
                    <DropdownMenuItem asChild>
                      <Link href="/admin" className="cursor-pointer">
                        Panneau d'Administration
                      </Link>
                    </DropdownMenuItem>
                  </>
                )}
                
                <DropdownMenuSeparator />
                <DropdownMenuItem className="cursor-pointer">
                  Paramètres
                </DropdownMenuItem>
                <DropdownMenuItem className="cursor-pointer">
                  Se déconnecter
                </DropdownMenuItem>
              </DropdownMenuContent>
            </DropdownMenu>
          </div>
        </div>
      </div>
    </nav>
  )
}
