"use client"

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { Input } from '@/components/ui/input'
import { Textarea } from '@/components/ui/textarea'
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog'
import { Label } from '@/components/ui/label'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Separator } from '@/components/ui/separator'
import { Navigation } from '@/components/navigation'
import { Utilisateur, Idee, Vote, Commentaire } from '@/types'

export default function HomePage() {
  const [currentUser] = useState<Utilisateur>({
    id: '1',
    nom: 'Jean Dupont',
    email: 'jean.dupont@entreprise.com',
    points: 150,
    badge: 'Innovateur',
    role: 'EMPLOYE'
  })

  const [idees, setIdees] = useState<Idee[]>([
    {
      id: '1',
      titre: 'Amélioration de l\'espace de travail',
      document: 'Proposition pour réorganiser les bureaux',
      categorie: 'ENVIRONNEMENT',
      description: 'Réorganiser l\'espace de travail pour améliorer la productivité et le bien-être des employés.',
      date_creation: new Date('2024-01-15'),
      date_modif: new Date('2024-01-15'),
      status: 'EN_ATTENTE',
      validePar: '',
      utilisateur_id: '1',
      utilisateur: {
        id: '1',
        nom: 'Jean Dupont',
        email: 'jean.dupont@entreprise.com',
        points: 150,
        badge: 'Innovateur',
        role: 'EMPLOYE'
      },
      votes: [],
      commentaires: []
    },
    {
      id: '2',
      titre: 'Application mobile interne',
      document: 'Développement d\'une app mobile',
      categorie: 'TECHNOLOGIE',
      description: 'Créer une application mobile pour faciliter la communication interne.',
      date_creation: new Date('2024-01-10'),
      date_modif: new Date('2024-01-12'),
      status: 'VALIDEE',
      validePar: 'Admin',
      utilisateur_id: '2',
      utilisateur: {
        id: '2',
        nom: 'Marie Martin',
        email: 'marie.martin@entreprise.com',
        points: 200,
        badge: 'Expert',
        role: 'EMPLOYE'
      },
      votes: [],
      commentaires: []
    }
  ])

  const [newIdee, setNewIdee] = useState<{
    titre: string
    categorie: 'TECHNOLOGIE' | 'ENVIRONNEMENT' | 'PROCESSUS' | 'COMMUNICATION' | 'AUTRE' | ''
    description: string
    document: string
  }>({
    titre: '',
    categorie: '',
    description: '',
    document: ''
  })

  const handleSubmitIdee = () => {
    if (newIdee.categorie === '') return
    
    const idee: Idee = {
      id: Date.now().toString(),
      titre: newIdee.titre,
      document: newIdee.document,
      categorie: newIdee.categorie,
      description: newIdee.description,
      date_creation: new Date(),
      date_modif: new Date(),
      status: 'EN_ATTENTE',
      validePar: '',
      utilisateur_id: currentUser.id,
      utilisateur: currentUser,
      votes: [],
      commentaires: []
    }
    
    setIdees([...idees, idee])
    setNewIdee({ titre: '', categorie: '', description: '', document: '' })
  }

  const handleVote = (ideeId: string, typeVote: number) => {
    // Logique de vote
    console.log(`Vote ${typeVote} pour l'idée ${ideeId}`)
  }

  const getStatusBadge = (status: string) => {
    switch (status) {
      case 'EN_ATTENTE':
        return <Badge variant="secondary">En attente</Badge>
      case 'VALIDEE':
        return <Badge variant="default">Validée</Badge>
      case 'REJETEE':
        return <Badge variant="destructive">Rejetée</Badge>
      default:
        return <Badge variant="outline">Inconnu</Badge>
    }
  }

  return (
    <div className="min-h-screen bg-background">
      <Navigation currentUser={currentUser} />
      
      <div className="container mx-auto p-6 max-w-7xl">
        {/* Header */}
        <div className="mb-8">
          <h1 className="text-4xl font-bold text-foreground">Système de Gestion des Idées</h1>
          <p className="text-muted-foreground mt-2">Partagez vos idées et contribuez à l'innovation</p>
        </div>

      <Tabs defaultValue="toutes" className="w-full">
        <div className="flex justify-between items-center mb-6">
          <TabsList className="grid w-full max-w-md grid-cols-3">
            <TabsTrigger value="toutes">Toutes les idées</TabsTrigger>
            <TabsTrigger value="mes-idees">Mes idées</TabsTrigger>
            <TabsTrigger value="suivies">Suivies</TabsTrigger>
          </TabsList>

          <Dialog>
            <DialogTrigger asChild>
              <Button className="bg-primary text-primary-foreground hover:bg-primary/90">
                Soumettre une idée
              </Button>
            </DialogTrigger>
            <DialogContent className="sm:max-w-[600px]">
              <DialogHeader>
                <DialogTitle>Soumettre une nouvelle idée</DialogTitle>
                <DialogDescription>
                  Partagez votre idée avec l'équipe. Tous les champs sont requis.
                </DialogDescription>
              </DialogHeader>
              <div className="grid gap-4 py-4">
                <div className="grid gap-2">
                  <Label htmlFor="titre">Titre de l'idée</Label>
                  <Input
                    id="titre"
                    value={newIdee.titre}
                    onChange={(e) => setNewIdee({...newIdee, titre: e.target.value})}
                    placeholder="Entrez le titre de votre idée"
                  />
                </div>
                <div className="grid gap-2">
                  <Label htmlFor="categorie">Catégorie</Label>
                  <Select onValueChange={(value: 'TECHNOLOGIE' | 'ENVIRONNEMENT' | 'PROCESSUS' | 'COMMUNICATION' | 'AUTRE') => setNewIdee({...newIdee, categorie: value})}>
                    <SelectTrigger>
                      <SelectValue placeholder="Sélectionnez une catégorie" />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="TECHNOLOGIE">Technologie</SelectItem>
                      <SelectItem value="ENVIRONNEMENT">Environnement</SelectItem>
                      <SelectItem value="PROCESSUS">Processus</SelectItem>
                      <SelectItem value="COMMUNICATION">Communication</SelectItem>
                      <SelectItem value="AUTRE">Autre</SelectItem>
                    </SelectContent>
                  </Select>
                </div>
                <div className="grid gap-2">
                  <Label htmlFor="description">Description</Label>
                  <Textarea
                    id="description"
                    value={newIdee.description}
                    onChange={(e) => setNewIdee({...newIdee, description: e.target.value})}
                    placeholder="Décrivez votre idée en détail"
                    rows={4}
                  />
                </div>
                <div className="grid gap-2">
                  <Label htmlFor="document">Document/Illustration</Label>
                  <Input
                    id="document"
                    value={newIdee.document}
                    onChange={(e) => setNewIdee({...newIdee, document: e.target.value})}
                    placeholder="Lien vers un document ou une illustration (optionnel)"
                  />
                </div>
              </div>
              <div className="flex justify-end space-x-2">
                <Button variant="outline">Annuler</Button>
                <Button onClick={handleSubmitIdee}>Soumettre l'idée</Button>
              </div>
            </DialogContent>
          </Dialog>
        </div>

        <TabsContent value="toutes" className="space-y-6">
          <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
            {idees.map((idee) => (
              <Card key={idee.id} className="hover:shadow-lg transition-shadow">
                <CardHeader>
                  <div className="flex justify-between items-start">
                    <CardTitle className="text-lg">{idee.titre}</CardTitle>
                    {getStatusBadge(idee.status)}
                  </div>
                  <CardDescription>
                    Par {idee.utilisateur.nom} • {idee.date_creation.toLocaleDateString()}
                  </CardDescription>
                </CardHeader>
                <CardContent>
                  <p className="text-sm text-muted-foreground mb-3">{idee.description}</p>
                  <div className="flex items-center justify-between">
                    <Badge variant="outline">{idee.categorie}</Badge>
                    <div className="flex space-x-2">
                      <Button
                        size="sm"
                        variant="outline"
                        onClick={() => handleVote(idee.id, 1)}
                      >
                        👍 {idee.votes.filter(v => v.typeVote === 1).length}
                      </Button>
                      <Button
                        size="sm"
                        variant="outline"
                        onClick={() => handleVote(idee.id, -1)}
                      >
                        👎 {idee.votes.filter(v => v.typeVote === -1).length}
                      </Button>
                    </div>
                  </div>
                  <Separator className="my-3" />
                  <div className="flex justify-between text-sm text-muted-foreground">
                    <span>{idee.commentaires.length} commentaires</span>
                    <span>Modifié le {idee.date_modif.toLocaleDateString()}</span>
                  </div>
                </CardContent>
              </Card>
            ))}
          </div>
        </TabsContent>

        <TabsContent value="mes-idees" className="space-y-6">
          <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
        {idees.filter(idee => idee.utilisateur_id === currentUser.id).map((idee) => (
              <Card key={idee.id} className="hover:shadow-lg transition-shadow">
                <CardHeader>
                  <div className="flex justify-between items-start">
                    <CardTitle className="text-lg">{idee.titre}</CardTitle>
                    {getStatusBadge(idee.status)}
                  </div>
                  <CardDescription>
                    Créée le {idee.date_creation.toLocaleDateString()}
                  </CardDescription>
                </CardHeader>
                <CardContent>
                  <p className="text-sm text-muted-foreground mb-3">{idee.description}</p>
                  <div className="flex items-center justify-between">
                    <Badge variant="outline">{idee.categorie}</Badge>
                    <div className="flex space-x-2">
                      <Button size="sm" variant="outline">
                        👍 {idee.votes.filter(v => v.typeVote === 1).length}
                      </Button>
                      <Button size="sm" variant="outline">
                        👎 {idee.votes.filter(v => v.typeVote === -1).length}
                      </Button>
                    </div>
                  </div>
                  <Separator className="my-3" />
                  <div className="flex justify-between text-sm text-muted-foreground">
                    <span>{idee.commentaires.length} commentaires</span>
                    <span>Modifié le {idee.date_modif.toLocaleDateString()}</span>
                  </div>
                </CardContent>
              </Card>
            ))}
          </div>
        </TabsContent>

        <TabsContent value="suivies" className="space-y-6">
          <div className="text-center py-12">
            <p className="text-muted-foreground">Aucune idée suivie pour le moment.</p>
            <p className="text-sm text-muted-foreground mt-2">
              Cliquez sur le bouton "Suivre" sur les idées qui vous intéressent.
            </p>
          </div>
        </TabsContent>
      </Tabs>
      </div>
    </div>
  )
}
